import React, { useState, useRef } from "react";
import { StyleSheet, css } from "aphrodite";

import { useWindowWidth } from "@react-hook/window-size";

// https://github.com/brainhubeu/react-carousel
// https://brainhubeu.github.io/react-carousel/docs/api/carousel
import Carousel, { Dots } from "@brainhubeu/react-carousel";
import "@brainhubeu/react-carousel/lib/style.css";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Slider from "react-slick";

import colors from "common/colors";
import homeStyles from "home/homeStyles";

import { FaArrowLeft, FaArrowRight } from "react-icons/fa";

// 2 x 3
// 3456 x 5184
import brick from "home/images/carousel/brick.jpg";
import tree from "home/images/carousel/tree.jpg";
import white from "home/images/carousel/white.jpg";
import manhattanTree from "home/images/carousel/manhattan-tree.jpg";

const IMAGES = [brick, tree, white, manhattanTree];

// TODO use https://kenwheeler.github.io/slick/

export default () => {
  const sliderRef = useRef(null);
  const windowWidth = useWindowWidth(
    360 /* initialWidth when there is no window */,
    { wait: 100 }
  );
  const isBigScreen = windowWidth > 1100;
  const handleClick = e => {
    const isTapOnLeftHalf = e.pageX < window.innerWidth / 2;
    if (isTapOnLeftHalf) {
      sliderRef.current.slickPrev();
    } else {
      sliderRef.current.slickNext();
    }
  };
  return (
    <div
      className={css(styles.container)}
      onClick={isBigScreen ? null : handleClick}
    >
      <Slider
        ref={sliderRef}
        centerPadding="0px"
        className="slider variable-width"
        slidesToShow={isBigScreen ? 2 : 1}
        slidesToScroll={isBigScreen ? 2 : 1}
        draggable={isBigScreen}
        touchMove={false}
        dots
      >
        {IMAGES.map((image, index) => (
          <img
            key={index}
            className={css(styles.image)}
            src={image}
            alt={`gallery preview ${index}`}
          />
        ))}
      </Slider>
    </div>
  );
};

const styles = StyleSheet.create({
  container: {
    marginTop: "60px",
    marginBottom: "60px",
    marginLeft: "auto",
    marginRight: "auto",
    width: "50vh",
    "@media (min-width: 1100px)": {
      width: "900px"
    },
    "@media (min-width: 601px)": {
      marginTop: "90px",
      marginBottom: "90px"
    }
  },
  dots: {
    paddingTop: "20px"
  },
  arrow: {
    height: "20px",
    width: "20px",
    padding: "20px",
    borderRadius: "30px",
    ":hover": {
      cursor: "pointer",
      backgroundColor: colors.defaultGray
    },
    userSelect: "none",
    color: colors.textGray,
    margin: "20px",
    backgroundColor: colors.selectedGray
  },
  image: {
    maxWidth: "50vh",
    opacity: 1,
    transition: "opacity .5s"
  }
});
